import listItem from '@/components/listItem.vue'
import Search from '@/components/index/search.vue'
import { mapState} from 'vuex'
import Time from '@/common/time.js'
export default {
	data(){
		return{
			list:[],
			limit:8,
			page:1,
			refresh:true,
			newpage:null,
			count:null,
			loadingText:''
		}
	},
	components:{
		listItem,
		Search
	},
	computed:{
		...mapState({
			sessionKey:state=>state.sessionKey
		})
	},
	methods:{
		getLearnList(){
			uni.showLoading({
				title: '加载中...'
			});
			this.$api.classify({
				sessionKey:this.sessionKey,
				class:'learn',
				page:this.page,
				limit:this.limit
			}).then(res=>{
				this.list=res.goods;
				uni.hideLoading();
			})
		},
		addMore(){//加载更多数据
			let that=this;
			console.log(that.page)
			that.loadingText='加载更多...';
			uni.showNavigationBarLoading();
			that.page++;
			that.$api.classify('/good/class',{
				sessionKey:that.sessionKey,
				page:that.page,
				class:'learn',
				limit:that.limit
			})
			.then(res=>{
				if(res.goods.length==0){
					that.page--;
					uni.hideNavigationBarLoading();
					that.loadingText='已加载完全部';
				}
				that.list=that.list.concat(res.goods)
			})
		},
		sortDesc(){//倒序
			let that=this;
			this.refresh=false;
			this.newpage=Math.ceil(this.count/this.limit);
			let limit=that.count%that.limit;
			that.$api.classify('/good/class',{
				sessionKey:that.sessionKey,
				page:that.newpage,
				class:'learn',
				limit:that.limit
			}).then(res=>{
				that.list=res.goods
				that.newpage--;
			})
		},
		descMore(){//倒序加载更多数据
			if(this.newpage<=0){
				this.loadingText='已加载完全部';
				return false;
			}else{
				let that=this;
				this.loadingText='加载更多...'
				uni.showNavigationBarLoading();
				that.$api.classify('/good/class',{
					sessionKey:that.sessionKey,
					page:that.newpage,
					class:'learn',
					limit:that.limit
				}).then(res=>{
					that.list=that.list.concat(res.goods)
					that.newpage--;
					uni.hideNavigationBarLoading();
				})
			}
		},
		moveHandle(){
		}
	},
	onReachBottom(){
		let that=this;
		if(this.refresh){
			setTimeout(()=>{
				that.addMore()
			},500)
		}else{
			setTimeout(()=>{
				that.descMore()
			},500)
		}
	},
	onPullDownRefresh(){
		this.page=1;
		if(this.refresh){
			this.getLearnList();
			uni.stopPullDownRefresh();
		}else{
			this.sortDesc()
			uni.stopPullDownRefresh();
		}
	},
	onLoad() {
		uni.startPullDownRefresh()
	}
}
